import ColorBoxCombo from './base/ColorBoxCombo.js';

/**
 * @module TaskBoard/widget/TaskColorCombo
 */

/**
 * A combo populated with predefined colors usable by a task, see {@link TaskBoard.model.TaskModel#field-eventColor}.
 *
 * Used in {@link TaskBoard.widget.TaskEditor} to pick a color for a task. Double click a task to try it:
 *
 * {@inlineexample TaskBoard/widget/TaskColorCombo.js}
 *
 * @classtype taskcolorcombo
 * @extends TaskBoard/widget/base/ColorBoxCombo
 * @inputfield
 */
export default class TaskColorCombo extends ColorBoxCombo {
    static $name = 'TaskColorCombo';

    static type = 'taskcolorcombo';

    static configurable = {
        valueField        : 'color',
        clearable         : true,
        placeholder       : 'None',
        showBoxForNoColor : true,
        items             : [
            { color : 'red', text : 'Red' },
            { color : 'pink', text : 'Pink' },
            { color : 'purple', text : 'Purple' },
            { color : 'deep-purple', text : 'Deep purple' },
            { color : 'indigo', text : 'Indigo' },
            { color : 'blue', text : 'Blue' },
            { color : 'light-blue', text : 'Light blue' },
            { color : 'cyan', text : 'Cyan' },
            { color : 'teal', text : 'Teal' },
            { color : 'green', text : 'Green' },
            { color : 'light-green', text : 'Light green' },
            { color : 'lime', text : 'Lime' },
            { color : 'yellow', text : 'Yellow' },
            { color : 'amber', text : 'Amber' },
            { color : 'orange', text : 'Orange' },
            { color : 'deep-orange', text : 'Deep orange' }
        ]
    };
}

TaskColorCombo.initClass();
